.input-tag {
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  display: flex;
  flex-wrap: wrap;
  padding: 5px 5px 0;
  height: calc(1.5em + 0.75rem + 2px);
}

.input-tag input {
  border: none;
  width: 100%;
}

.input-tag input:focus {
  outline: 0;
}

.input-tag__tags {
  display: inline-flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  width: 100%;
}

.input-tag__tags li {
  align-items: center;
  background: #bac7d9;
  border-radius: 2px;
  color: black;
  display: flex;
  list-style: none;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 2px 4px;
}

.input-tag__tags li button {
  align-items: center;
  appearance: none;
  background: #333333;
  border: none;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  display: inline-flex;
  font-size: 14px;
  height: 15px;
  justify-content: center;
  line-height: 0;
  margin-left: 8px;
  padding: 4px;
  transform: rotate(45deg);
  width: 15px;
}

.input-tag__tags li.input-tag__tags__input {
  background: none;
  flex-grow: 1;
  padding: 0;
}
